import React, { useEffect, useState } from "react";
import Icon from "../icons/icons";
import "./tikets.css"





const Tikets=(props)=>{
  const [mesagesSend,setMesagesSend]=useState([
    {sender:' ',position:' ',reeded:true, subject:'      ',messageTypeOf:1},
    {sender:'  ',position:'',reeded:false, subject:'   ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:false,position:' ', subject:'     ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1},
    {sender:' ',reeded:true ,position:'', subject:'    ',messageTypeOf:1}
])
  const [massageType,setMassageType]=useState('1');
  const [selectedStyle1,setSelectedStyle1]=useState("mesagetype");
  const [selectedStyle2,setSelectedStyle2]=useState("mesagetype mesagetype6");
  const [selectedStyle3,setSelectedStyle3]=useState("mesagetype");
  useEffect(()=>{
    setMesagesSend(mesagesSend)

  })
  const mouseEnterHandeler =()=>{

  }
  const selecteMassageTypeHandeller=(event,key)=>{
    event.preventDefault()
    setMassageType(key)
    switch(key){
      case 0:
        setSelectedStyle1("mesagetype mesagetype6");
        setSelectedStyle2("mesagetype ");
        setSelectedStyle3("mesagetype ");
        break;
      case 1:
        setSelectedStyle1("mesagetype");
        setSelectedStyle2("mesagetype mesagetype6");
        setSelectedStyle3("mesagetype");
        break;
      case 2:
        setSelectedStyle1("mesagetype");
        setSelectedStyle2("mesagetype");
        setSelectedStyle3("mesagetype mesagetype6");
        break;
    }

  }
  const acceptHandeler=(event,index)=>{
    event.preventDefault();
    const mesagesSend1=[...mesagesSend];
    console.log(mesagesSend1[index].messageTypeOf)
    mesagesSend1[index].messageTypeOf=0;
    console.log(mesagesSend1[index].messageTypeOf)
    setMesagesSend(mesagesSend1);

  }
    return(
      <div className="disp2">
            <div className="messagestype">
                <div className={selectedStyle1} onClick={(event)=>{selecteMassageTypeHandeller(event,0)}} >   </div>
                <div className={selectedStyle2} onClick={(event)=>{selecteMassageTypeHandeller(event,1)}} >   </div>
                <div className={selectedStyle3} onClick={(event)=>{selecteMassageTypeHandeller(event,2)}} >   </div>             
              </div> 
            <div className="dispMsg">
                {mesagesSend.map((item,index)=>{
                    return( item.messageTypeOf==massageType?
                    <div className="messagestype listmesage">
                        <div className="mesagetype2 mesagetype5">{index+1}</div>
                        <div className="mesagetype2 mesagetype4">{item.sender} ({item.position}) </div>
                        <div className="mesagetype2 mesagetype3">{item.subject}</div>
                        <div className="">
                            {item.messageTypeOf=='1'?<button className="btnMsg" onClick={(event)=>{acceptHandeler(event,index)}}></button> :<span></span>}
                            {item.messageTypeOf=='1'?<button className="btnMsg"></button> :<span></span>}
                            {<button className="btnMsg"></button>}
                        </div>
                    </div>
                    :<span></span>
                    );   
                })}

              </div>
                         
      </div>

    );
}
export default Tikets