import React, { useState } from "react";
import "./inputfile.css"


const Inputfile=(props)=>{
    const [files, setFiles] = useState([]);
    const [Elements,setElements]=useState(<div></div>)


    const handleChange = (event) => {
        const filescopy=files;
        filescopy.push(...event.target.files)
      setFiles(filescopy);
      const Elementsnew=files.map((items,index)=>{
        return(
            <div className="FileLists" key={index}>
              {items.name}
            </div>
        );
      })
      setElements(Elementsnew);
    };
    const uploadFiles = async () => {
        //         API
      };
      const clearclass=files.length>0?"clearclassvisibel":"clearclassinvisibel";
      const clearClicked=()=>{
        setFiles([]);
        setElements(<div></div>)
      }
    return(
        <div className="fiList">
            {Elements}
            <label className={clearclass} onClick={clearClicked}> </label>
        <label for="files" class="btn3">+</label>
         <input id="files" type="file"   multiple onChange={handleChange} className="inputf"  />
        </div>

    );
}
export default Inputfile 

