import React from 'react';
import { BrowserRouter, Routes, Route } from "react-router-dom";
import './App.css';
import Login from './layouts/login';
import Dash from './layouts/dash';
import Inbox from './layouts/inbox';
import Managers from './layouts/managers';
import RecoveryPass from './layouts/recoverypass';
import Register from './layouts/newuser';


function App() {
  return (
    <div className="App">
    <BrowserRouter  basename="/Mihan">
      <Routes>
        <Route path="/" exact element={<Login></Login>}></Route>
        <Route path="/register" exact element={<Register></Register>}></Route>
        <Route path="/PasswordRecovery"  exact element={<RecoveryPass></RecoveryPass>}></Route>
        <Route path="/dash"  exact element={<Dash></Dash>}></Route>
      </Routes>
    </BrowserRouter>
    </div>
  );
}

export default App;
