import React, { useState } from "react";
import {Link, BrowserRouter, Routes, Route } from "react-router-dom";
import Icon from "../component/icons/icons";
import "./dash.css"
import Systems from "../component/systems/systems";
import Req from "../component/req/req";
import Inbox from "./inbox";
import Managers from "./managers";
import Tikets from "../component/tikets/tikets";
import ReqTikets from "../component/reqtikets/reqtikets";
import ToolbarIcon from "../assests/toolbarIcon.png"
import ConnectWithUs from "../component/connect-with-us/connectwithus";
import logo from "../assests/Mihan.png"
import prof from "../assests/.jpg"
import logo1 from "../assests/tiket.png"
import logo2 from "../assests/tiket2.jpg"
import logo3 from "../assests/inbox.png"
import logo4 from "../assests/request.jpg"
import logo5 from "../assests/analyz.jpg"
import logo6 from "../assests/systems.jpg"

const Dash=(props)=>{
    const [element,setElement]=useState(<div></div>)
    const onClickHandeler1=()=>{
      setElement(<div className="disp"><Tikets></Tikets></div>)

    }
    const onClickHandeler2=()=>{
      setElement(<div className="disp"><ReqTikets></ReqTikets></div>)

    }
    const onClickHandeler3=()=>{
      setElement(<div className="disp"><Inbox></Inbox></div>)

    }
    const onClickHandeler4=()=>{
      setElement(<div className="disp"><Req></Req></div>)

    }
    const onClickHandeler5=()=>{
      setElement(<div className="disp"><Managers></Managers></div>)

    }
    const conectWithUsHandeler=()=>{
      setElement(<div className="disp"><ConnectWithUs></ConnectWithUs></div>)
    }
    const onClickHandeler6=()=>{
      setElement(<div className="disp"><Systems></Systems></div>)

    }
    const [isClicked, setIsClicked] = useState(true);

    const handleClickIcon = () => {
      setIsClicked(!isClicked);
      setTimeout(()=>setIsClicked(true),"5000")
    };

    
    


    return(
        <React.Fragment >
        <div className="toolbar">
          <div className="logoprof">
            <img src={logo} width="60" height="35" className="logo" ></img>
            <img src={prof} width="32" height="35" className="prof"></img>
            <div className="proflabel"> </div>
            <div className="profileinf">
                <img src={prof} width="55" height="62" className="prof"></img>
                <div className="proflabel2">   </div>
                <div className="proflabel2"> : </div>
                  <button className="profbtn">
                      
                  </button>
                  <button className="profbtn">
                      
                  </button>
            </div>
          </div>
          <div className="noting">
          </div>
          <div className="homepages">
          <img src={ToolbarIcon} width="30" height="30" className="prof2" onClick={handleClickIcon}
          style={{border:isClicked ?"none":"solid 1px white"}}></img>
            <div className="homepagex">  </div>
            <div className="homepagex">  </div>
            
            <div className="homepagex" onClick={conectWithUsHandeler}>   </div>
          </div>
        </div>
    
        <div className="menubar">
          <div className={isClicked?"icons icons2":"icons"} >
            <Icon LOgo={logo1} label={" "} Clicked={onClickHandeler1}></Icon>
            <Icon LOgo={logo2}  label={"   "} Clicked={onClickHandeler2}></Icon>
            <Icon LOgo={logo3} label={"  "}  Clicked={onClickHandeler3}></Icon>
            <Icon LOgo={logo4} label={"  "} Clicked={onClickHandeler4}></Icon>
            <Icon LOgo={logo5}  label={" "}  Clicked={onClickHandeler5}></Icon>
            <Icon LOgo={logo6} label={""}  Clicked={onClickHandeler6}></Icon>
          </div>
        </div>
          {element}
      </React.Fragment>

    );
}
export default Dash
